@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #303030;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  letter-spacing: -0.01em;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

._visually-hidden:not(:focus):not(:active),
input[type=checkbox]._visually-hidden,
input[type=radio]._visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

._button {
  text-decoration: none;
  font-size: 18px;
  font-weight: 100;
  line-height: inherit;
  vertical-align: middle;
  text-align: center;
  border-radius: 25px;
  display: inline-block;
  max-width: 270px;
  padding: 0.5em 1.5em;
  background-image: linear-gradient(0deg, #ff7500 0%, #ffa600 100%);
  color: #ffffff;
  border: 2px solid #ff8a00;
}
._button:hover {
  color: #000000;
  background-color: #f2f2f2;
  background-image: none;
  border: 2px solid #000000;
}

._button--transparent {
  text-decoration: none;
  font-size: 18px;
  font-weight: 100;
  line-height: inherit;
  vertical-align: middle;
  text-align: center;
  border-radius: 25px;
  display: inline-block;
  max-width: 270px;
  padding: 0.5em 1.5em;
  color: #000000;
  background-color: transparent;
  border: 2px solid #ff8a00;
}
._button--transparent:hover {
  color: #000000;
  background-color: #f2f2f2;
  background-image: none;
  border: 2px solid #000000;
}

._main {
  width: calc(100% - 30px);
  margin: 0 auto;
  padding-top: 50px;
}

@media (min-width: 768px) {
  ._main {
    width: 92%;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1147px) {
  ._main {
    width: 1147px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
._company-info {
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 28px;
  padding-right: 28px;
  margin-bottom: 40px;
  background-color: #f2f2f2;
}

._company-info__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

._company-info__text {
  padding: 0;
  margin-top: 0;
  margin-bottom: 15px;
}

._company-info__link {
  width: 59px;
  margin-right: 17px;
  flex-shrink: 0;
  margin-bottom: 15px;
}

@media (min-width: 768px) {
  ._company-info__wrap {
    align-items: center;
  }
}
@media (min-width: 1147px) {
  ._company-info {
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 75px;
  }
}
._services {
  padding-top: 10px;
  margin-bottom: 50px;
}

._services {
  display: flex;
  flex-direction: column;
}

._services__title {
  position: relative;
  padding-left: 25px;
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 22px;
  line-height: 26px;
  color: #333333;
  text-align: center;
  font-weight: bold;
  letter-spacing: 0;
}
._services__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  background-color: #f98900;
  border-radius: 50%;
}

._services__block-title {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 38px;
  font-size: 20px;
  line-height: 24px;
}

._services__wrap {
  position: relative;
  margin-bottom: 30px;
}
._services__wrap::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 60px;
  height: 60px;
  background-image: linear-gradient(60deg, #00cbfe 7%, #004cb3 92%);
  border-radius: 10px;
}
._services__wrap::after {
  content: "";
  position: absolute;
  top: 44px;
  left: 30px;
  transform: translate(-50%, -50%);
}

._services__wrap--installation::after {
  width: 54px;
  height: 54px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icon-montazh.png");
}

._services__wrap--dismantling::after {
  width: 56px;
  height: 56px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icon-demontazh.png");
}

._services__list {
  margin: 0;
  padding-top: 5px;
  padding-left: 20px;
}

._services__item {
  margin-bottom: 6px;
}

._services__text {
  margin-top: 0;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  ._services {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  ._services__wrap {
    width: 48%;
    padding-left: 90px;
    box-sizing: border-box;
  }
  ._services__wrap::before {
    width: 70px;
    height: 70px;
  }
  ._services__wrap::after {
    top: 50px;
    left: 35px;
  }
  ._services__wrap:nth-child(2n+1) {
    padding-right: 18px;
  }
  ._services__title {
    width: 100%;
    text-align: left;
    font-size: 26px;
    line-height: 28px;
  }
  ._services__block-title {
    text-align: left;
  }
}
@media (min-width: 768px) {
  ._services__block-title {
    margin-bottom: 12px;
  }
  ._services__list {
    margin-left: -10px;
  }
}
@media (min-width: 1147px) {
  ._services__wrap {
    padding-left: 110px;
  }
  ._services__wrap::before {
    width: 90px;
    height: 90px;
  }
  ._services__wrap::after {
    top: 60px;
    left: 45px;
  }
}
._callback {
  position: relative;
  min-height: 259px;
  padding: 40px;
  margin-bottom: 25px;
  box-sizing: border-box;
  background-image: url("../img/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 25px;
  box-shadow: 0px 11px 54px 0px rgba(60, 60, 60, 0.4);
}

._callback__title {
  padding: 0;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 22px;
  line-height: 26px;
  color: #333333;
  text-align: center;
  font-weight: bold;
  letter-spacing: 0;
}

._callback__text {
  margin-top: 0;
  margin-bottom: 27px;
}

._callback__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 40px;
}

._callback__link {
  margin-right: 10px;
  margin-bottom: 10px;
}

._callback__img {
  display: block;
  margin: 0 auto;
}

@media (min-width: 768px) {
  ._callback__title {
    font-size: 26px;
    line-height: 32px;
  }
}
@media (min-width: 1147px) {
  ._callback {
    border-radius: 10px;
  }
  ._callback__title {
    max-width: 670px;
    text-align: left;
  }
  ._callback__img {
    position: absolute;
    top: -30px;
    right: 55px;
  }
  ._callback__links {
    display: block;
    margin-bottom: 0;
  }
}
._products {
  padding-top: 50px;
  margin-bottom: 47px;
}

._products__slide {
  padding-top: 35px;
  padding-bottom: 40px;
}

._products__title {
  position: relative;
  padding-left: 25px;
  margin: 0;
  font-size: 22px;
  line-height: 26px;
  color: #333333;
  text-align: center;
  font-weight: bold;
}
._products__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  background-color: #f98900;
  border-radius: 50%;
}

@media (min-width: 768px) {
  ._products__title {
    font-size: 26px;
    line-height: 28px;
    text-align: left;
  }
}
._product {
  position: relative;
  padding-bottom: 30px;
  min-height: 395px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

._product__fig {
  margin: 0;
  padding: 0;
  text-align: center;
}

._product__img {
  border-radius: 6px;
}

._product__figcaption {
  padding-top: 2px;
  margin-bottom: 20px;
  font-weight: bold;
  text-align: left;
}
._product__figcaption span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
}

._product__params {
  margin-bottom: 20px;
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

._product__price {
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  padding: 5px;
  background-color: #0064c1;
  border-radius: 3px;
}

._product__size {
  font-size: 14px;
  line-height: 18px;
  color: #a7a7a7;
  padding: 5px;
  border: 1px solid #a7a7a7;
  border-radius: 3px;
}

._product__order {
  box-sizing: border-box;
  display: block;
  margin-top: auto;
}

._product__link {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  margin-top: auto;
}

._product__link:hover {
  box-shadow: 0px 4px 27px 0px rgba(70, 67, 67, 0.51);
}

._product:hover ._product__order {
  display: block;
}

._advantages__title {
  position: relative;
  padding-left: 25px;
  font-size: 22px;
  line-height: 24px;
  margin: 0;
  margin-bottom: 50px;
  color: #333333;
  text-align: center;
  font-weight: bold;
  letter-spacing: 0;
}
._advantages__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  background-color: #f98900;
  border-radius: 50%;
}

._advantages__list, ._advantages__list-add {
  margin-top: 0;
  margin-bottom: 30px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
}

._advantages__item {
  position: relative;
  text-align: center;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}
._advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

._advantages__item--first::before {
  width: 239px;
  height: 130px;
  background-image: url("../img/item-3000.png");
}

._advantages__item--second::before {
  top: 10px;
  width: 229px;
  height: 120px;
  background-image: url("../img/item-2500.png");
}

._advantages__item--third::before {
  top: 10px;
  width: 110px;
  height: 115px;
  background-image: url("../img/item-1.png");
}

._advantages__item span {
  display: block;
  margin-bottom: 128px;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  font-weight: normal;
}

._advantages__item-add {
  position: relative;
  padding-left: 80px;
  box-sizing: border-box;
  margin-bottom: 60px;
  text-align: center;
}
._advantages__item-add::before {
  content: "";
  position: absolute;
  top: -13px;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #f2f2f2;
}
._advantages__item-add::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 55px;
  height: 55px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

._advantages__item-add--first::after {
  background-image: url("../img/icon-fast.png");
}

._advantages__item-add--second::after {
  background-image: url("../img/icon-options.png");
}

._advantages__item-add--third::after {
  background-image: url("../img/icon-time.png");
}

._advantages__item-add--forth::after {
  background-image: url("../img/icon-moving.png");
}

@media (min-width: 768px) {
  ._advantages__list, ._advantages__list-add {
    flex-direction: row;
    margin-bottom: 40px;
  }
  ._advantages__item {
    width: 32%;
    box-sizing: border-box;
  }
  ._advantages__title {
    font-size: 26px;
    line-height: 28px;
    text-align: left;
  }
  ._advantages__item-add {
    width: 45%;
    padding-left: 100px;
    text-align: left;
  }
  ._advantages__item-add::before {
    width: 80px;
    height: 80px;
  }
  ._advantages__item-add::after {
    top: 10px;
    left: 10px;
  }
}
@media (min-width: 1147px) {
  ._advantages__item-add {
    width: 23%;
    padding-left: 115px;
    box-sizing: border-box;
  }
  ._advantages__item-add::before {
    width: 100px;
    height: 100px;
  }
  ._advantages__item-add::after {
    top: 50%;
    left: 20px;
    transform: translate(0, -50%);
  }
}
._reviews {
  position: relative;
  margin-bottom: 55px;
}

._reviews__slide {
  padding-bottom: 40px;
}

._reviews__title {
  position: relative;
  padding-left: 25px;
  margin: 0;
  margin-bottom: 50px;
  font-size: 22px;
  line-height: 24px;
  color: #333333;
  text-align: center;
  font-weight: bold;
}
._reviews__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  background-color: #f98900;
  border-radius: 50%;
}

@media (min-width: 800px) {
  ._reviews-prev {
    position: absolute;
    top: 44%;
    left: 10px;
    width: 13px;
    height: 21px;
    background-image: url("../img/arrow.svg");
    background-size: contain;
    background-repeat: no-repeat;
  }
  ._reviews-next {
    position: absolute;
    top: 44%;
    right: 10px;
    width: 13px;
    height: 21px;
    background-image: url("../img/arrow.svg");
    background-size: contain;
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
}
@media (min-width: 768px) {
  ._reviews__title {
    font-size: 26px;
    line-height: 28px;
    text-align: left;
  }
}
._review {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #f2f2f2;
  background-image: url("../img/review-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 6px;
  height: 100%;
  padding-top: 30px;
  padding-bottom: 50px;
  padding-left: 30px;
  padding-right: 30px;
