}

._review--odd {
  background-image: url("../img/review-bg-2.jpg");
}

._review__fig {
  padding: 0;
  margin: 0;
}

._review__img {
  border-radius: 50%;
  margin-bottom: 20px;
}

._review__figcaption {
  font-size: 20px;
  line-height: 24px;
  font-weight: bold;
  color: #151611;
  margin-bottom: 17px;
}

._review__text {
  padding-right: 15px;
  line-height: 23px;
}

@media (min-width: 768px) {
  ._review {
    align-items: stretch;
    text-align: left;
  }
}
