    height: 21px;
    background-image: url("../img/arrow.svg");
    background-size: contain;
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
}
@media (min-width: 768px) {
  ._clients__title {
    font-size: 26px;
    line-height: 28px;
    text-align: left;
  }
}
